
import Button from "../../components/inputs/Button.vue";
import Input from "../../components/inputs/Input.vue";
import Password from "../../components/inputs/PasswordInput.vue";
import LoadingModal from "../../components/modals/LoadingModal.vue";
import Modal from "../../components/modals/MainModal.vue";
import { reactive, onMounted, ref, defineComponent } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import gsap from "gsap";
export default defineComponent({
  components: {
    Button,
    Input,
    Password,
    Modal,
    LoadingModal
  },
  setup() {
    const loginData = reactive({
      email: "",
      password: ""
    });
    const store = useStore();
    const router = useRouter();
    const loading = ref(false);

    async function login() {
      const errors = document.querySelectorAll(".error");
      if (
        !errors.length &&
        loginData.email !== "" &&
        loginData.password !== ""
      ) {
        loading.value = true;

        //Fetch
        await store.dispatch("login", loginData);
        loading.value = false;

        //Reset fields
        loginData.email = "";
        loginData.password = "";

        //Modal
        if (store.state.isLogIn) {
          router.push({ path: "/dashboard/start" });
        } else {
          store.dispatch("setModal", {
            show: true,
            error: true,
            message: store.state.error
          });
        }
      }
    }

    async function isLoggedIn() {
      await store.dispatch("isLoggedIn");

      if (store.state.isLogIn) router.push({ path: "/dashboard/start" });
    }

    isLoggedIn();

    onMounted(() => {
      const tl = gsap.timeline({});

      if (window.innerWidth < 1000) {
        tl.from(".login__label", {
          duration: 0.7,
          opacity: 0
        })
          .addLabel("form")
          .fromTo(
            ".login__form",
            {
              duration: 0.5,
              opacity: 0,
              y: 200,
              ease: "ease-in"
            },
            {
              opacity: 1,
              y: -80
            }
          )
          .to(
            ".login__label",
            {
              duration: 0.5,
              y: -100
            },
            "form"
          );
      } else {
        tl.fromTo(
          ".login__label",
          {
            duration: 0.5,
            x: -150,
            y: -70,
            opacity: 0
          },
          {
            y: -70,
            opacity: 1,
            x: 0
          }
        ).fromTo(
          ".login__form",
          {
            duration: 0.5,
            opacity: 0,
            y: 100
          },
          {
            opacity: 1,
            y: -50,
            ease: "ease-in"
          }
        );
      }
    });

    return { loginData, login, loading };
  }
});
