
import axios from "axios";
import BackButton from "../components/Back.vue";
import Checkbox from "../components/inputs/Checkbox.vue";
import Button from "../components/inputs/Button.vue";
import RateModal from "../components/modals/RateModal.vue";
import LoadingModal from "../components/modals/LoadingModal.vue";
import Modal from "../components/modals/MainModal.vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { ref, defineComponent, onMounted, reactive } from "vue";
import { polishTranslate } from "../data";
export default defineComponent({
  components: {
    BackButton,
    Checkbox,
    RateModal,
    Modal,
    LoadingModal,
    Button
  },
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  setup(prp) {
    interface Book {
      id: number;
      rates: number[];
      tags: string[];
      name: string;
      author: string;
      img: string;
      slug: string;
      room: string;
      place: string;
      series: string;
      description: string;
    }
    let data = reactive({} as Book);

    const router = useRouter();

    const store = useStore();
    const rateModal = ref(false);

    function capitalize(val: string) {
      return val.slice(0, 1).toUpperCase() + val.slice(1);
    }

    //Search book
    data = store.state.results.find(
      (item: object & { slug: string }) => item.slug === prp.slug
    );

    const loading = ref(false);
    //Rate book
    async function rateBook(rate: number) {
      rateModal.value = false;
      loading.value = true;
      try {
        await axios.put("/api/books/rate", {
          rate: rate,
          id: data.id
        });
        store.dispatch("setModal", {
          show: true,
          error: false,
          message: "Dzikujemy za opini!"
        });
      } catch (err) {
        store.dispatch("setModal", {
          show: true,
          error: true,
          message: err.response.data.message
        });
      } finally {
        loading.value = false;
      }
    }

    //Bookmark book
    const activeBookmark = ref(false);
    async function addBookToLibrary() {
      loading.value = true;
      try {
        await axios.put("/api/books/bookmark", {
          id: data.id
        });
        store.dispatch("setModal", {
          show: true,
          error: false,
          message: "Dodano ksik do biblioteki."
        });
        activeBookmark.value = true;
      } catch (err) {
        store.dispatch("setModal", {
          show: true,
          error: true,
          message: err.response.data.message
        });
      } finally {
        loading.value = false;
      }
    }

    //Search by tags
    function searchByTag(tag: string) {
      router.push({ path: "/results" });
      store.dispatch("advancedSearch", { tags: [tag] });
    }

    //Search by series
    function searchBySeries(series: string) {
      router.push({ path: "/results" });
      store.dispatch("searchBySeries", series);
    }

    if (!data) {
      router.push({ path: "/" });
      router.go(-1);
    }

    onMounted(() => {
      window.scrollTo(0, 0);
    });

    return {
      data,
      capitalize,
      rateModal,
      rateBook,
      addBookToLibrary,
      activeBookmark,
      searchByTag,
      searchBySeries,
      loading,
      polishTranslate
    };
  }
});
