
import { defineComponent, ref, onUpdated } from "vue";
import { HTMLInputEvent } from "../../types";
export default defineComponent({
  name: "Input",
  props: {
    name: {
      type: String,
      required: true
    },
    type: {
      type: String,
      default: "text"
    },
    modelValue: {
      type: String,
      required: true
    },
    validate: {
      type: Boolean,
      default: true
    }
  },
  setup(prp, ctx) {
    const error = ref("");
    const inputFocused = ref(false);
    const inputColor = ref("#bebebe");
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    function validateInput(e: HTMLInputEvent) {
      ctx.emit("update:modelValue", e.target.value);
      if (prp.validate) {
        if (prp.type === "email") {
          if (!emailRegex.test(e.target.value)) {
            error.value = "Wprowad poprawny adres email.";
            inputColor.value = "#e5383b";
          } else {
            error.value = "";
            inputColor.value = "green";
          }
        } else {
          if (e.target.value.trim().length < 3) {
            error.value = `${prp.name} musi zawiera conajmniej 3 znaki.`;
            inputColor.value = "#e5383b";
          } else {
            error.value = "";
            inputColor.value = "green";
          }
        }
      }
    }

    onUpdated(() => {
      if (prp.modelValue.trim() === "") {
        inputColor.value = "#bebebe";
        error.value = "";
      }
    });

    return { validateInput, error, inputFocused, inputColor };
  }
});
